body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.checkout__ad {
	width: 100%;
	margin-bottom: 10px;
}
.checkout__title {
	margin-right: 10px;
	padding: 10px;
	border-bottom: 1px solid lightgray;
}
.checkout {
	display: flex;
	padding: 20px;
	background-color: white;
	height: -webkit-max-content;
	height: max-content;
}

.checkoutProduct {
	display: flex;
	margin-top: 20px;
	margin-bottom: 20px;
}
.checkoutProduct__info {
	padding-left: 20px;
}
.checkoutProduct__info > button {
	background: #f0c14b;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}
.checkoutProduct__image {
	object-fit: contain;
	width: 180px;
	height: 180px;
}
.checkoutProduct__rating {
	display: flex;
}
.checkoutProduct__title {
	font-size: 17px;
	font-weight: 800;
}

.subtotal {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 300px;
	height: 100px;
	padding: 20px;
	background-color: #f3f3f3;
	border: 1px solid #dddddd;
	border-radius: 3px;
}
.subtotal__gift {
	display: flex;
	align-items: center;
	margin-top: 13px;
}
.subtotal__gift > input {
	margin-right: 5px;
}
.subtotal button {
	background: #f0c14b;
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}

.header {
	height: 60px;
	display: flex;
	align-items: center;
	background-color: #131921;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 100;
}
.header__logo {
	width: 100px;
	object-fit: contain;
	margin: 0 20px;
	margin-top: 18px;
}
.header__searchIcon {
	padding: 5px;
	height: 22px !important;
	background-color: #cd9042;
}
.header__search {
	display: flex;
	flex: 1 1;
	align-items: center;
	border-radius: 24px;
}
.header__searchInput {
	height: 11px;
	padding: 10px;
	border: none;
	width: 100%;
}
.header__option {
	display: flex;
	flex-direction: column;
	margin-left: 10px;
	margin-right: 10px;
	color: white;
}
.header__nav {
	display: flex;
	justify-content: space-evenly;
}
.header__optionLineOne {
	font-size: 10px;
}

.header__optionLineTwo {
	font-size: 13px;
	font-weight: 800;
}

.header__optionBasket {
	display: flex;
	align-items: center;
	color: white;
}
.header__basketCount {
	margin-left: 10px;
	margin-right: 10px;
}
.header__clearlink {
	text-decoration: none;
}

.home {
	/* display: flex;
	justify-content: center; */
	margin-left: auto;
	margin-right: auto;
	max-width: 1500px;
}
.home__image {
	width: 100%;
	z-index: -1;
	margin-bottom: -150px;
	-webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
	        mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
	display: flex;
	z-index: 1;
	margin-left: 5px;
	margin-right: 5px;
}

.product {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end;
	margin: 10px;
	padding: 20px;
	width: 100%;
	max-height: 400px;
	min-width: 100px;
	background-color: white;
	z-index: 1;
	box-shadow: 0 6px 18px -9px rgba(0, 0, 0, 0.75);
	transition: -webkit-transform 0.1s ease-in;
	transition: transform 0.1s ease-in;
	transition: transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
}
.product > img {
	max-height: 200px;
	width: 100%;
	object-fit: contain;
	margin-bottom: 15px;
}
.product__price {
	margin-top: 5px;
}

.product__info {
	height: 100px;
	margin-bottom: 15px;
}
.product > button {
	background: #f0c14b;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}
.product__rating {
	display: flex;
}
.product:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.login__logo {
	margin-top: 20px;
	margin-bottom: 20px;
	object-fit: contain;
	width: 100px;
	margin-right: auto;
	margin-left: auto;
}
.login {
	background-color: white;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.login__container {
	width: 300px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	display: flex;
	flex-direction: column;
	border-radius: 5px;
	border: 1px solid lightgray;
	padding: 20px;
}
.login__container > h1 {
	font-weight: 500;
	margin-bottom: 20px;
}
.login__container > form > h5 {
	margin-bottom: 5px;
}
.login__container > form > input {
	height: 30px;
	margin-bottom: 10px;
	background-color: white;
	width: 98%;
}

.login__container > p {
	margin-top: 15px;
	font-size: 12px;
}

.login__signInButton {
	background: #f0c14b;
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
}

.login__registerButton {
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: darkgray;
}

